import { css, html, LitElement } from 'lit';
import { customElement, property } from 'lit/decorators.js';

import { commonCss, inputCss } from '../styles';

@customElement('sdpi-button')
export class Button extends LitElement {
    static styles = [
        commonCss,
        inputCss,
        css`
            :host {
                --bg-color: #303030;
            }

            button {
                background-color: var(--bg-color);
                border: 1px solid #969696;
                border-radius: 3px;
                padding: var(--input-padding);
            }

            button:not(:disabled):hover {
                background-color: #464646;
                cursor: pointer;
            }

            button:not(:disabled):active {
                background-color: var(--bg-color);
                border-color: #646464;
                color: #969696;
            }

            button:disabled {
                opacity: 0.5;
            }
        `
    ];

    /**
     * Determines whether the button is disabled.
     */
    @property({ type: Boolean })
    public disabled = false;

    /**
     * The label displayed within the button.
     */
    @property()
    public label = '';

    /**
     * Renders the component.
     * @returns The HTML template used to render the component.
     */
    render() {
        super.onclick;
        return html`
            <div class="container">
                <div>&nbsp;</div>
                <div>
                    <button .disabled=${this.disabled}>${this.label}</button>
                </div>
            </div>
        `;
    }
}
